.pin {
  position: absolute;
  display: block;
  width: 70px;
  height: 70px;
  min-width: 50px;
  min-height: 50px;
  background-color: white;
  color: black;
  padding: 10px 20px;
  right: 80px;
  top: 85vh;
  border: 1px solid transparent;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.17;
  border-radius: 50%;
  background-image: linear-gradient(-45deg, rgba(255, 0, 0, 0.8), rgba(255, 165, 0, 0.8), rgba(255, 255, 0, 0.8), rgba(0, 128, 0, 0.8), rgba(0, 0, 255, 0.8), rgba(128, 0, 128, 0.8));
  -webkit-transition: all 400ms;
  transition: all 400ms; }
  .pin:hover, .pin:focus {
    outline: none;
    -webkit-transform: scale(1.03) rotate(360deg);
            transform: scale(1.03) rotate(360deg); }

.news {
  min-height: 500px;
  padding-bottom: 100px; }

.news h1 {
  font-weight: bold;
  font-size: 50px;
  line-height: 1.2; }

.news__list {
  display: none;
  list-style: none;
  margin: 0;
  overflow-x: scroll;
  padding: 30px 5px 30px 35px;
  position: relative;
  scrollbar-color: white #424242;
  scrollbar-width: auto; }
  .news__list::-webkit-scrollbar {
    height: 16px;
    background-color: white;
    border-radius: 25px; }
  .news__list::-webkit-scrollbar-thumb {
    background-color: #424242;
    border-radius: 25px; }
  .news__list--open {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }

.news-card {
  min-width: 300px;
  min-height: 350px;
  background-color: #000000;
  color: #ffffff;
  background-image: linear-gradient(85deg, #434343, #262626);
  padding: 15px 30px 15px 25px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  border-radius: 16px;
  position: relative;
  -webkit-transition: all 200ms;
  transition: all 200ms; }
  .news-card:not(.news-card:first-child) {
    -webkit-box-shadow: -16px 0 30px #000000;
            box-shadow: -16px 0 30px #000000; }
  .news-card:hover, .news-card:focus {
    -webkit-transform: translate(-15px, -5px) rotate(3deg);
            transform: translate(-15px, -5px) rotate(3deg);
    outline: none; }

.news-card__heading {
  font-weight: 600;
  font-size: 20px;
